body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(29,29,29);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.bg-color{
    background-color: black;
}

.text-color{
    color: white;
}
.container {
    position: relative;
  }
  
  .image {
    display: block;
    width: 100%;
    height: auto;
  }
  
  .overlay {
    position: absolute;
    top: -8px;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background-color: #008CBA;
  }
  
  .container:hover .overlay {
    opacity: 1;
  }
  
  .text {
    color:white;
    font-size: 20px;
    position: absolute;
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  
header{
    position: fixed;
    z-index: 1;
    width: 100%;
    top: 0;
    left: 0;
}


  .banner{
      margin-top: 350px;
      margin-bottom: 375px;
  }

  
.btn{
    border-radius: 5px;
    width: 7rem;
    height: 2rem;
    background-color: #333;
    color:white;
    cursor:pointer;
    border-color:rgb(162, 162, 248);
    
}
  
.btn:hover{
    background-color: black;
    box-shadow: 0px 0px 10px 2px white;
}

.view{
    top: 0;
    width:100%;
    height: 400px;
    position: absolute;
    
}

.view .animate:nth-child(1){ 
    top:400px;
    display: flex;
    width: 120px;
    height: 120px;
    position: absolute;
    border-radius: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 60px darkcyan, inset 0 0 60px darkcyan;
    -webkit-animation: movez 3s infinite linear alternate;
            animation: movez 3s infinite linear alternate;
}

.view .animate:nth-child(2){ 
    top:500px;
    display: flex;
    width: 120px;
    height: 120px;
    position: absolute;
    border-radius: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 60px crimson, inset 0 0 60px crimson;
    -webkit-animation: movez 2s  infinite linear;
            animation: movez 2s  infinite linear;
}

.view .animate:nth-child(3){
    top:400px;
    display: flex;
    width: 120px;
    height: 120px;
    position: absolute;
    border-radius: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 60px yellowgreen, inset 0 0 60px yellowgreen;
    -webkit-animation: movex 3s infinite linear;
            animation: movex 3s infinite linear;
}


@-webkit-keyframes movez {
    0% {
      -webkit-transform: rotateZ(0deg) translate3d(200px, 0, 0);
              transform: rotateZ(0deg) translate3d(200px, 0, 0);
    }
    100% {
      -webkit-transform: rotateZ(360deg) translate3d(200px, 0, 0);
              transform: rotateZ(360deg) translate3d(200px, 0, 0);
    }
  }


@keyframes movez {
    0% {
      -webkit-transform: rotateZ(0deg) translate3d(200px, 0, 0);
              transform: rotateZ(0deg) translate3d(200px, 0, 0);
    }
    100% {
      -webkit-transform: rotateZ(360deg) translate3d(200px, 0, 0);
              transform: rotateZ(360deg) translate3d(200px, 0, 0);
    }
  }

  @-webkit-keyframes movex {
    0%,100% {
      -webkit-transform: rotateY(0deg) translate3d(-100%, 0, 0);
              transform: rotateY(0deg) translate3d(-100%, 0, 0);
    }
    25% {
      -webkit-transform: rotateY(360deg) translate3d(100%, 0, 0);
              transform: rotateY(360deg) translate3d(100%, 0, 0);
    }
    50%{
      -webkit-transform: rotateX(0deg) translate3d(0, -100%, 0);
              transform: rotateX(0deg) translate3d(0, -100%, 0);
    }
    75%{
      -webkit-transform: rotateX(360deg) translate3d(0, 100%, 0);
              transform: rotateX(360deg) translate3d(0, 100%, 0);
    }
  }

  @keyframes movex {
    0%,100% {
      -webkit-transform: rotateY(0deg) translate3d(-100%, 0, 0);
              transform: rotateY(0deg) translate3d(-100%, 0, 0);
    }
    25% {
      -webkit-transform: rotateY(360deg) translate3d(100%, 0, 0);
              transform: rotateY(360deg) translate3d(100%, 0, 0);
    }
    50%{
      -webkit-transform: rotateX(0deg) translate3d(0, -100%, 0);
              transform: rotateX(0deg) translate3d(0, -100%, 0);
    }
    75%{
      -webkit-transform: rotateX(360deg) translate3d(0, 100%, 0);
              transform: rotateX(360deg) translate3d(0, 100%, 0);
    }
  }

  a:hover button .up {
      -webkit-animation:up 1s infinite linear;
              animation:up 1s infinite linear;
  }
  
  @-webkit-keyframes up {
      0%,100%{-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0)}
      50%{-webkit-transform: translate3d(0,-7px,0);transform: translate3d(0,-7px,0)}
  }
  
  @keyframes up {
      0%,100%{-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0)}
      50%{-webkit-transform: translate3d(0,-7px,0);transform: translate3d(0,-7px,0)}
  }

  a:hover .spin {
      -webkit-animation: spin 0.5s 2 linear;
              animation: spin 0.5s 2 linear;
  }
  @-webkit-keyframes spin {
      0%{-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg)}
      100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg)}
  }
  @keyframes spin {
      0%{-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg)}
      100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg)}
  }



